import React, { useState } from "react";

function SearchCity({changeCity}) {
  const [city, setCity] = useState("");

  const handleClick = (e) => {
    e.preventDefault();
    if(city === "") return
    changeCity(city);
    setCity("");
  };

  return (
    <form onClick={handleClick}>
      <input
        type="text"
        placeholder="Minalin, Candaba, Angeles City"
        value={city}
        name="city"
        onChange={(e) => setCity(e.target.value)}
      />
      <input type="submit" value="Search" />
    </form>
  );
}

export default SearchCity;
