import React, { useState, useEffect } from "react";
import "./Home.css";

import SearchCity from "./SearchCity";

const Home = () => {
  const [city, setCity] = useState("Manila");
  const [data, setData] = useState("");

  const getWeather = async (city) => {
    try {
      const res = await fetch(
        `http://api.openweathermap.org/data/2.5/weather?q=${city}&units=metric&APPID=33b6c97cbe9b5cc44cfea7be5826600f`,
        { mode: "cors" }
      );
      const data = await res.json();
      setData({
        city: data.name,
        country: data.sys.country,
        temp: Math.round(data.main.temp),
        temp_min: Math.round(data.main.temp_min),
        temp_max: Math.round(data.main.temp_max),
        temp_feels: Math.round(data.main.feels_like),
        icon: data.weather[0].icon,
        weather_condition: data.weather[0].main,
        weather_description: data.weather[0].description,
      });
    } catch (err) {
      console.log(err);
    }
  };

  const changeCity = (city) => {
    setCity(city);
  };

  useEffect(() => {
    getWeather(city);
    console.log("effect run")

  });

  

  return (
    <div className="home-container">
      <div className="search-city-container">
        <SearchCity changeCity={changeCity} />
      </div>

      <div className="display-weather-container">
        <div className="first-panel">
          <div>{`${data.city}, ${data.country}`}</div>
          <div>{`${data.temp}째C`}</div>
          <div>{`Feels like ${data.temp_feels}째C`}</div>
        </div>

        <div className="second-panel">
          <img
            src={`https://openweathermap.org/img/wn/${data.icon}@4x.png`}
            alt="weather-icon"
          />
        </div>

        <div className="third-panel">
          <div>{`Min: ${data.temp_min}째C`}</div>
          <div>{`Max: ${data.temp_max}째C`}</div>
        </div>

        <div className="fourth-panel">
          <div>{`${data.weather_description}`}</div>
        </div>
      </div>
    </div>
  );
};

export default Home;
