import React from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";

import "./App.css";

//components
import Header from "./components/Header/Header";
import Home from "./components/Home/Home";
import About from "./components/About/About";

function App() {
  return (
    <Router>
      <div className="App">
      <Header />
      <Switch>
      <Route exact path="/weatherino-react/" component={Home} />
      <Route exact path="/weatherino-react/about" component={About} />
      </Switch>
      </div>
  
    </Router>
  );
}

export default App;
